import {View} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import React from 'react';

import {Users} from './api';

import Home from './panels/Home';
import VueDefault from './panels/VueDefault';
import VueFriends from './panels/VueFriends';
import VueMyLikes from './panels/VueMyLikes';
import VueOutgoingRequests from './panels/VueOutgoingRequests';

//https://oauth.vk.com/authorize?client_id=6076877&display=page&redirect_uri=&scope=likes+notifications+post+wall+friends+status&response_type=token&v=&state=

class App extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            activePanel: 'home',
            fetchedUser: null,
            access_token: '77e1bd20937265f0c7459629d57a028d284fc0f4c5c78669076d4340ebb438d6faa3d50f623da22619479',
        };
    }

    componentDidMount () {
        this.getUserInfo();
    }

    getUserInfo () {
        Users.getUsers({
            fields: 'photo_200,status,city',
            access_token: this.state.access_token
        })
            .then(data => {
                this.setState({fetchedUser: data.response[0]});
            })
            .catch(e => console.error(e));
    }

    go = (e) => {
        this.setState({activePanel: e.currentTarget.dataset.to});
    };

    render () {
        return (
            <View activePanel={this.state.activePanel}>
                <Home id="home" fetchedUser={this.state.fetchedUser} go={this.go}
                      access_token={this.state.access_token}/>
                <VueDefault
                    id="default"
                    go={this.go}
                    access_token={this.state.access_token}
                />
                <VueFriends
                    id="friends"
                    go={this.go}
                    access_token={this.state.access_token}
                />
                <VueOutgoingRequests
                    id="subscribers"
                    go={this.go}
                    access_token={this.state.access_token}
                />
                <VueMyLikes
                    id="my-likes"
                    go={this.go}
                    access_token={this.state.access_token}
                />
            </View>
        );
    }
}

export default App;
