import { Panel, Button, Group, Div, Avatar, PanelHeader, HeaderButton, Cell, IOS, Spinner, Progress } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import { Friends, Users } from '../api';

class VueFriends extends React.Component {
	constructor(props) {
		super(props);
		this.state = {

		};
	}
	componentDidMount() {

	}
	render() {
		return (
			<Panel id={this.props.id}>
                <PanelHeader
					left={<HeaderButton onClick={this.props.go} data-to="home">
						{this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					Стандартный шаблон
				</PanelHeader>

			</Panel>
		);
	}
}

VueFriends.propTypes = {
	go: PropTypes.func.isRequired,
	access_token: PropTypes.string.isRequired
};

export default VueFriends;
