import {
    Panel,
    Button,
    Group,
    Div,
    Avatar,
    PanelHeader,
    HeaderButton,
    Cell,
    IOS,
    Tabs,
    TabsItem,
    Gallery,
    Progress,
    CellButton
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import {Likes} from '../api';

class VueFriends extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            activeTab4: 'dial',
            postsLikes: [],
            progressCount: 0,
            progressStart: 0
        };
    }

    getLikesPosts () {
        let count = 100;
        let offset = 0;
        this.getLikesInterval = setInterval(() => {
            Likes.getLikePosts({
                count: count,
                offset: offset,
                access_token: this.props.access_token
            })
                .then(res => {
                    if (res.error) {
                        console.error('Ошибка получения лайков', res);
                    } else if (!res.response.items.length) {
                        clearInterval(this.getLikesInterval);
                    } else {
                        this.setState((state) => {
                            state.postsLikes.push(...res.response.items);
                            state.progressStart = state.postsLikes.length;
                        });
                        offset += count;
                        this.forceUpdate();
                    }
                })
                .catch(e => console.error(e));
        }, 3000);
    }

    deleteAllLikes () {
        this.delLikesInterval = setInterval(() => {
            if (!this.state.postsLikes.length) {
                clearInterval(this.delLikesInterval);
            } else {
                Likes.deleteLike({
                    type: 'post',
                    owner_id: this.state.postsLikes[0].owner_id,
                    item_id: this.state.postsLikes[0].id,
                    access_token: this.props.access_token
                })
                    .then(res => {
                        if (res.error) {
                            console.error('Ошибка удаления', res.error);
                        } else {
                            this.state.postsLikes.splice(0, 1);
                            this.setState(state => state.progressCount++);
                            // this.forceUpdate();
                        }
                    })
                    .catch(e => console.error(e));
            }
        }, 3000);
    }

    cancelDelLikes () {
        clearInterval(this.delLikesInterval);
    }

    prev (e) {
        clearInterval(this.getLikesInterval);
        clearInterval(this.delLikesInterval);
        this.props.go(e);
    }

    componentDidMount () {
        this.getLikesPosts();
    }

    render () {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.prev} data-to="home">
                        {this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >
                    Мои лайки
                </PanelHeader>
                <Group title="">
                    <Div style={{display: 'flex', zIndex: 100}}>
                        <Button size="l" stretched style={{marginRight: 8}} onClick={this.deleteAllLikes.bind(this)}>Снять
                            все лайки</Button>
                        <Button size="l" stretched level="secondary"
                                onClick={this.cancelDelLikes.bind(this)}>Отмена</Button>
                    </Div>
                    {
                        this.state.postsLikes &&
                        <Cell description={`удалено ${this.state.progressCount} / ${this.state.progressStart}`}>
                            <Progress value={this.state.progressCount}/>
                        </Cell>
                    }
                </Group>
                <Group>
                    <Div>
                        У вас найдено {this.state.progressStart} лайков, <br/>
                        Вы можете забрать все свои лайки со всех записей!
                    </Div>
                </Group>
            </Panel>
        );
    }
}

VueFriends.propTypes = {
    go: PropTypes.func.isRequired,
    access_token: PropTypes.string.isRequired
};

export default VueFriends;
