import {Panel, ListItem, Button, Group, Div, Avatar, PanelHeader} from '@vkontakte/vkui';

import React from 'react';

import PropTypes from 'prop-types';

const Home = ({id, go, fetchedUser}) => (
    <Panel id={id}>
        <PanelHeader>ВК Настройки</PanelHeader>
        {fetchedUser &&
        <Group title="Аккаунт">
            <ListItem
                before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
                description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
            >
                {`${fetchedUser.first_name} ${fetchedUser.last_name}`}
            </ListItem>
        </Group>
        }

        <Group title="Панель управления друзьями и заявками">
            <Div>
                <Button size="xl" level="2" onClick={go} data-to="friends">
                    Друзья
                </Button>
            </Div>
            <Div>
                <Button size="xl" level="2" onClick={go} data-to="subscribers">
                    Исходящие заявки
                </Button>
            </Div>
            <Div>
                <Button size="xl" level="2" onClick={go} data-to="default">
                    Подписчики
                </Button>
            </Div>
        </Group>
        <Group title="Панель управления лайками">
            <Div>
                <Button size="xl" level="2" onClick={go} data-to="my-likes">
                    мои лайки
                </Button>
            </Div>
        </Group>
    </Panel>
);

Home.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    access_token: PropTypes.string.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
};

export default Home;
