import {
    Panel,
    Button,
    Group,
    Div,
    Avatar,
    PanelHeader,
    HeaderButton,
    Cell,
    IOS,
    Spinner,
    Progress
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import {Friends, Users} from '../api';

class VueFriends extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            progress: 0,
            countFriends: 0,
            isDelAllFriends: false,
            usersInfo: [],
            isPreloader: true
        };
    }

    getFriends () {
        let offsetFriendsInterval = 0;
        let isClearInterval = false;
        this.friendsInterval = setInterval(() => {
            if (isClearInterval) clearInterval(this.friendsInterval);

            Friends.getFriends({
                count: 100,
                offset: offsetFriendsInterval,
                access_token: this.props.access_token
            })
                .then(data => {
                    offsetFriendsInterval += 100;
                    this.setState({countFriends: data.response.count});
                    this.getUsersInfo(data.response.items)
                        .then(data => {
                            if (data.error) {
                                console.error('Ошибка', data.error);
                            } else {
                                isClearInterval = !data.response.length;
                                this.setState((state) => {
                                    state.usersInfo.push(...data.response);
                                    state.countFriends = data.count;
                                    state.isPreloader = false;
                                });
                                this.forceUpdate();
                            }
                        })
                        .catch(e => console.error(e));
                })
                .catch(e => console.error(e));
        }, 2000);
    }

    getUsersInfo (ids) {
        const strFriendsIds = ids.slice(0, 100).join(',');
        return Users.getUsers({
            user_ids: strFriendsIds,
            fields: 'photo_200_orig,status',
            access_token: this.props.access_token
        });
    }

    deleteFriend (user_id) {
        return () => {
            Friends.deleteFriends({
                user_id,
                access_token: this.props.access_token
            })
                .then(data => {
                    if (data.error) {
                        console.error('Ошибка удаления', data.error);
                    } else {
                        const indexDelFriend = this.state.usersInfo.findIndex(item => item.id === user_id);
                        this.state.usersInfo.splice(indexDelFriend, 1);
                        this.forceUpdate();
                    }
                })
                .catch(e => console.error(e));
        };
    }

    deleteAllFriends () {
        const isClearInterval = !this.state.usersInfo.length;
        this.setState({isDelAllFriends: true});
        this.intervalDeleteAllFriends = setInterval(() => {
            if (isClearInterval) clearInterval(this.intervalDeleteAllFriends);

            Friends.deleteFriends({
                user_id: this.state.usersInfo[0].id,
                access_token: this.props.access_token
            })
                .then(data => {
                    if (data.error) {
                        console.error('Ошибка удаления', data.error);
                    } else {
                        this.state.usersInfo.splice(0, 1);
                        this.setState(state => state.progress++);
                        this.forceUpdate();
                    }
                })
                .catch(e => console.error(e));
        }, 3000);
    }

    cancelDelFriends () {
        clearInterval(this.interval);
        this.setState({isDelAllFriends: false});
    }

    prev (e) {
        clearInterval(this.subscribersInterval);
        clearInterval(this.friendsInterval);
        this.props.go(e);
    }

    componentDidMount () {
        this.getFriends();
    }

    render () {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.prev.bind(this)} data-to="home">
                        {this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >
                    Друзья
                </PanelHeader>
                <Group title="">
                    <Div style={{display: 'flex', zIndex: 100}}>
                        <Button size="l" stretched style={{marginRight: 8}} onClick={this.deleteAllFriends.bind(this)}>Удалить
                            всех</Button>
                        <Button size="l" stretched level="secondary"
                                onClick={this.cancelDelFriends.bind(this)}>Отмена</Button>
                    </Div>
                    {
                        this.state.isDelAllFriends &&
                        <Cell description={`удалено ${this.state.progress} / ${this.state.countFriends}`}>
                            <Progress value={this.state.progress}/>
                        </Cell>
                    }
                </Group>
                <Group title={`Друзья ${this.state.usersInfo.length}`}>
                    {
                        this.state.isPreloader &&
                        <Cell size="l">
                            <Spinner size="large" style={{margin: '150px 0'}}/>
                        </Cell>
                    }
                    {
                        this.state.usersInfo.map((friend, index) => {
                            return (
                                <Cell
                                    size="l"
                                    description={friend.status || 'Статус не установлен'}
                                    before={<Avatar src={friend.photo_200_orig}/>}
                                    bottomContent={<Button onClick={this.deleteFriend(friend.id)}>Удалить</Button>}
                                    key={index}
                                >
                                    {`${friend.first_name} ${friend.last_name}`}
                                </Cell>
                            );
                        })
                    }
                </Group>
            </Panel>
        );
    }
}

VueFriends.propTypes = {
    go: PropTypes.func.isRequired,
    access_token: PropTypes.string.isRequired
};

export default VueFriends;
