import { Panel, Button, Group, Div, Avatar, PanelHeader, HeaderButton, Cell, IOS, Spinner, Progress } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import { Friends, Users } from '../api';

class VueOutgoingRequests extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			progress: 0,
			isDelAllFriends: false,
			countOutgoingRequests: 0,
			usersInfo: [],
			isPreloader: true
		};
	}
	getOutgoingRequests () {
		let offsetSubscribersInterval = 0;
		let isClearInterval = false;
		this.subscribersInterval = setInterval(() => {
			if (isClearInterval) clearInterval(this.subscribersInterval);

			Friends.getRequests({
				count: 100,
				offset: offsetSubscribersInterval,
				out: 1,
				access_token: this.props.access_token
			})
			.then(data => {
				offsetSubscribersInterval += 100;
				this.setState({countOutgoingRequests: data.response.count});
				this.getUsersInfo(data.response.items)
					.then(data => {
						if(data.error) {
							console.error('Ошибка', data.error);
						} else {
							isClearInterval = !data.response.length;
							this.setState((state) => {
								state.usersInfo.push(...data.response);
								state.isPreloader = false;
							});
							this.forceUpdate();
						}
					})
					.catch(e => console.error(e));
			})
			.catch(e => console.error(e));
		}, 2000);
	}
	getUsersInfo (ids) {
		const strFriendsIds = ids.slice(0, 100).join(',');
		return Users.getUsers({
			user_ids: strFriendsIds,
			fields: 'photo_200_orig,status',
			access_token: this.props.access_token
		});
	}
	deleteFriend (user_id) {
		return () => {
			Friends.deleteFriends({
				user_id,
				access_token: this.props.access_token
			})
			.then(data => {
				if(data.error) {
					console.error('Ошибка удаления', data.error);
				} else {
					const indexDelFriend = this.state.usersInfo.findIndex(item => item.id === user_id);
					this.state.usersInfo.splice(indexDelFriend, 1);
					this.forceUpdate();
				}
			})
			.catch(e => console.error(e));
		};
	}
	deleteAllFriends () {
		const isClearInterval = !this.state.usersInfo.length;
		this.setState({isDelAllFriends: true});
		this.intervalDeleteAllFriends = setInterval(() => {
			if (isClearInterval) clearInterval(this.intervalDeleteAllFriends);

			Friends.deleteFriends({
				user_id: this.state.usersInfo[0].id,
				access_token: this.props.access_token
			})
			.then(data => {
				if(data.error) {
					console.error('Ошибка удаления', data.error);
				} else {
					this.state.usersInfo.splice(0, 1);
					this.setState(state => state.progress++);
					this.forceUpdate();
				}
			})
			.catch(e => console.error(e));
		}, 3000);
	}
	cancelDelFriends () {
		clearInterval(this.interval);
		this.setState({isDelAllFriends: false });
	}
	prev (e) {
		clearInterval(this.subscribersInterval);
		clearInterval(this.intervalDeleteAllFriends);
		this.props.go(e);
	}
	componentDidMount() {
		this.getOutgoingRequests();
	}
	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader
					left={<HeaderButton onClick={this.prev.bind(this)} data-to="home">
						{this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					Исходящие заявки
				</PanelHeader>
				<Group title="">
					<Div style={{display: 'flex', zIndex: 100}}>
						<Button size="l" stretched style={{ marginRight: 8 }} onClick={this.deleteAllFriends.bind(this)}>Отменить у всех</Button>
						<Button size="l" stretched level="secondary" onClick={this.cancelDelFriends.bind(this)}>Отмена</Button>
					</Div>
					{this.state.isDelAllFriends &&
						<Cell description={`Отменено ${this.state.progress} / ${this.state.countOutgoingRequests}`}>
							<Progress value={this.state.progress} />
						</Cell>
					}
				</Group>
				<Group title={`Исходящие заявки в друзья ${this.state.usersInfo.length}`}>
					{	this.state.isPreloader &&
						<Cell size="l">
							<Spinner size="large" style={{ margin: '150px 0' }} />
						</Cell>
					}
					{
						this.state.usersInfo.map((friend, index) => {
							return (
								<Cell
									size="l"
									description={friend.status || 'Статус не установлен'}
									before={<Avatar src={friend.photo_200_orig} />}
									bottomContent={<Button onClick={this.deleteFriend(friend.id)}>Отменить заявку</Button>}
									key={index}
								>
								{`${friend.first_name} ${friend.last_name}`}
								</Cell>
							);
						})
					}
				</Group>
			</Panel>
		);
	}
}

VueOutgoingRequests.propTypes = {
	go: PropTypes.func.isRequired,
	access_token: PropTypes.string.isRequired
};

export default VueOutgoingRequests;
